import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import { gsap } from 'gsap';
import { Container, Form, Button, ListGroup } from 'react-bootstrap';

const App = () => {
  const [tasks, setTasks] = useState([]);
  const [newTask, setNewTask] = useState({ title: '', description: '' });
  const listRef = useRef(null);

  useEffect(() => {
    fetchTasks();
  }, []);

  const fetchTasks = async () => {
    try {
      const response = await axios.get('/api/tasks');
      setTasks(response.data);
    } catch (error) {
      console.error(error);
    }
  };

  const addTask = async () => {
    try {
      const response = await axios.post('/api/tasks', newTask);
      setTasks([...tasks, response.data]);
      setNewTask({ title: '', description: '' });

      // Animate the newly added task
      gsap.fromTo(
        listRef.current.lastChild,
        { opacity: 0, y: -20 },
        { opacity: 1, y: 0, duration: 0.3 }
      );
    } catch (error) {
      console.error(error);
    }
  };

  const updateTask = async (taskId, updatedTask) => {
    try {
      const response = await axios.put(`/api/tasks/${taskId}`, updatedTask);
      const updatedTasks = tasks.map((task) =>
        task._id === taskId ? response.data : task
      );
      setTasks(updatedTasks);
    } catch (error) {
      console.error(error);
    }
  };

  const deleteTask = async (taskId) => {
    try {
      await axios.delete(`/api/tasks/${taskId}`);
      const updatedTasks = tasks.filter((task) => task._id !== taskId);
      setTasks(updatedTasks);
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <Container className="d-flex flex-column align-items-center ml-3">
      <h1 className="mt-3">Task Manager</h1>
      <Form className="w-50 mt-3">
        <Form.Group>
          <Form.Control
            type="text"
            placeholder="Title"
            value={newTask.title}
            onChange={(e) => setNewTask({ ...newTask, title: e.target.value })}
          />
        </Form.Group>
        <Form.Group>
          <Form.Control
            type="text"
            placeholder="Description"
            value={newTask.description}
            onChange={(e) =>
              setNewTask({ ...newTask, description: e.target.value })
            }
          />
        </Form.Group>
        <Button onClick={addTask}>Add Task</Button>
      </Form>
      <ListGroup className="w-50 mt-3" ref={listRef}>
        {tasks.map((task) => (
          <ListGroup.Item key={task._id}>
            <Form.Control
              type="text"
              value={task.title}
              onChange={(e) =>
                updateTask(task._id, { ...task, title: e.target.value })
              }
            />
            <Form.Control
              type="text"
              value={task.description}
              onChange={(e) =>
                updateTask(task._id, { ...task, description: e.target.value })
              }
            />
            <Button
              variant="danger"
              onClick={() => {
                deleteTask(task._id);
                // Animate the deletion of the task
                gsap.to(listRef.current.lastChild, {
                  opacity: 0,
                  y: 20,
                  duration: 0.3,
                  onComplete: () => {
                    listRef.current.removeChild(listRef.current.lastChild);
                  },
                });
              }}
            >
              Delete
            </Button>
          </ListGroup.Item>
        ))}
      </ListGroup>
    </Container>
  );
};

export default App;





